*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --bg-color: #f6f6f6;
  --primary-color: #111;
  --logo-text: #000;
  --header-bg-color: #fff;
  --sidebar-activ-text: #4f2ee8;
  --scroll-color: #e0e0e0;
  --other-text-color: #111;
  --auth-border-color: #111;
  --auth-btn-text-color: #f3f3f3;
  --congrat-text-color: rgba(17, 17, 17, .5);
  --shopping-empty: rgba(0, 0, 0, .6);
  --shopping-arrow-left: #b4afaf;
  --shopping-arrow-right: #000;
  --shopping-current-page: #000;
  --shopping-current-page-text: #fff;
  --logout-text: #111;
  --logout-fon: #fff;
  --footer-bg-color: var(--bg-color);
  --second-color: #b4afaf;
  --nav-mobile-menu: #111;
  --shopping-border-list: rgba(79, 46, 232, .4);
  --blue-color: #4f2ee8;
  --yellow-color: #eac645;
  --white-color: #fff;
}

:root body.dark {
  --logout-fon: #4f2ee8;
  --logout-text: #fff;
  --bg-color: #202024;
  --primary-color: #000;
  --logo-text: #f3f3f3;
  --header-bg-color: #111;
  --sidebar-activ-text: #eac645;
  --scroll-color: rgba(17, 17, 17, .6);
  --other-text-color: #fff;
  --auth-border-color: #f6f6f6;
  --auth-btn-text-color: #202024;
  --congrat-text-color: rgba(255, 255, 255, .5);
  --shopping-empty: rgba(255, 255, 255, .6);
  --shopping-arrow-left: #fff;
  --shopping-current-page: #fff;
  --shopping-current-page-text: #000;
  --footer-bg-color: #606060;
}

@font-face {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(DM Sans Regular), url("DMSans-Regular.dac2326d.woff") format("woff");
}

@font-face {
  font-family: DM Sans;
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: local(DM Sans Italic), url("DMSans-Italic.e947e2a0.woff") format("woff");
}

@font-face {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(DM Sans Medium), url("DMSans-Medium.ea7c3d0c.woff") format("woff");
}

@font-face {
  font-family: DM Sans;
  font-style: bold;
  font-weight: 700;
  font-display: swap;
  src: local(DM Sans Bold), url("DMSans-Bold.c55f0cba.woff") format("woff");
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--bg-color);
  color: var(--primary-color);
  font-family: DM Sans, sans-serif;
  font-style: normal;
}

body::-webkit-scrollbar {
  width: 8px;
  background-color: var(--bg-color);
}

body::-webkit-scrollbar-thumb {
  background-color: var(--scroll-color);
  border-radius: 12px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

a:focus, button:focus {
  outline: 1px solid rgba(0, 0, 0, 0);
}

button {
  cursor: pointer;
  border: none;
  padding: 0;
}

address {
  font-style: normal;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  max-width: 375px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

.wrapper {
  flex-direction: column-reverse;
  display: flex;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }

  .main {
    width: 100%;
  }

  .aside {
    display: flex;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 24px;
  }

  .wrapper {
    flex-direction: row-reverse;
  }

  .main {
    width: 996px;
  }

  .aside {
    width: 356px;
    margin-right: 40px;
    display: block;
  }
}

.header-container {
  border: 1.5px solid var(--logo-text);
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 18px 20px;
  display: flex;
}

.logo {
  margin-right: 8px;
  transition: transform .15s ease-in-out;
}

.logo:hover, .logo:focus {
  transform: scale(1.03);
}

.logo-images {
  align-items: center;
  display: flex;
}

.bookshelf-logo {
  fill: var(--logo-text);
  margin-left: 8px;
}

.header-nav {
  display: none;
}

.header-nav-list {
  align-items: center;
  gap: 16px;
  display: flex;
}

.header-nav-link {
  color: var(--other-text-color);
  letter-spacing: .04px;
  font-weight: 500;
  line-height: 1.28;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.header-nav-link:focus, .header-nav-link:hover {
  color: var(--yellow-color);
}

.header-nav-link:hover .js-bag-container {
  background-color: var(--yellow-color);
}

.shopping-list-header {
  color: var(--other-text-color);
  align-items: center;
  gap: 8px;
  display: flex;
}

.switcher-burger {
  align-items: center;
  display: flex;
}

.menu-burger {
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: 14px;
  padding: 0;
}

.menu-burger-open {
  stroke: var(--other-text-color);
}

.menu-burger-open:hover, .menu-burger-open:focus {
  stroke: var(--blue-color);
}

.switch {
  width: 40px;
  height: 20px;
  align-items: center;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background: linear-gradient(180deg, var(--blue-color) 0%, #dedede 100%);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(8, 8, 8, .1);
}

.slider:hover, .slider:focus {
  transform: scale(1.03);
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #fefefe;
  transition: all .4s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked + .slider, input:focus + .slider {
  background: linear-gradient(180deg, var(--blue-color) 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.login-btn {
  display: none;
}

#signUpBtnHeader {
  transition: transform .2s ease-in-out;
}

#signUpBtnHeader:focus, #signUpBtnHeader:hover {
  transform: scale(1.03);
}

.header-btn-wrapper {
  position: relative;
}

.arrow-right {
  stroke: var(--yellow-color);
}

.header-nav-link.current .cart {
  fill: currentColor;
}

.cart {
  fill: currentColor;
  position: relative;
}

.menu-burger-icon-open {
  stroke: var(--logo-text);
}

.menu-burger-icon-open:hover, .menu-burger-icon-open:focus {
  stroke: var(--blue-color);
}

.current {
  color: var(--nav-mobile-menu);
  background-color: var(--yellow-color);
  border: none;
  border-radius: 36px;
  padding: 8px 16px;
  font-weight: 700;
}

.current:hover, .current:focus {
  background-color: var(--blue-color);
}

.header-arrow-down {
  fill: var(--header-bg-color);
}

.sign-up-btn-text.grow, .burger-sign-up-btn-text.grow {
  letter-spacing: -.02em;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.31818;
}

.log-out-btn {
  min-width: 118px;
  height: 48px;
  color: var(--header-bg-color);
  background-color: var(--logout-fon);
  border: 1.5px solid var(--nav-mobile-menu);
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  padding: 14px 24px;
  display: flex;
  position: absolute;
  top: 100%;
  left: 0;
}

.log-out-btn-text {
  letter-spacing: -.04em;
  color: var(--logout-text);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28571;
}

.log-out-arrow-right {
  stroke: var(--yellow-color);
}

.header-arrow-down {
  fill: var(--header-bg-color);
  transition: transform .3s;
}

.active-sign-up {
  transform: rotate(-180deg);
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  display: none;
}

.close-mobile-modal-btn.hidden {
  display: none;
}

@media only screen and (min-width: 768px) {
  .header-container {
    padding: 22px 32px;
  }
}

@media only screen and (min-width: 1440px) {
  .header-container {
