.mobile-menu-modal {
  width: 100%;
  height: 100%;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  justify-content: center;
  align-items: center;
  transition: transform .3s ease-in-out;
  position: fixed;
  top: 60px;
  left: -100%;
}

.mobile-menu-modal.open {
  transform: translateX(100%);
}

.js-open-menu-burger {
  display: block;
}

.js-open-menu-burger.hidden, .close-mobile-modal-btn.hidden {
  display: none;
}

@media screen and (min-width: 768px) {
  .mobile-menu-modal, .js-open-menu-burger {
    display: none;
  }
}

.mobile-menu-modal-content {
  height: 100vh;
  background-image: url("mobile-menu.74ab3146.png");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: 100%;
  flex-direction: column;
  padding: 20px 20px 80px;
  display: flex;
  position: relative;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .mobile-menu-modal-content {
    background-image: url("mobile-menu@2x.b9fefd77.png");
  }
}

.close-mobile-modal-btn {
  width: 28px;
  height: 28px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  margin-left: 14px;
  display: flex;
}

.mobile-modal-icon-close {
  stroke: var(--logo-text);
  transition: transition-duration .25s cubic-bezier(.4, 0, .2, 1);
}

.mobile-modal-icon-close:hover, .mobile-modal-icon-close:focus {
  stroke: var(--blue-color);
}

.mobile-menu-modal .login-modal {
  width: 184px;
  height: 48px;
  background-color: var(--blue-color);
  color: var(--white-color);
  border: 1.5px solid #111;
  border-radius: 18px;
  align-items: center;
  gap: 9px;
  margin-bottom: 126px;
  padding: 14px;
  font-weight: 500;
  display: flex;
}

.mobile-menu-modal .header-nav-mobile {
  margin-bottom: auto;
  display: flex;
}

.mobile-menu-modal .header-nav-list {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: auto;
  margin-right: auto;
}

.mobile-menu-modal .shopping-list-header, .mobile-menu-modal .cart {
  fill: currentColor;
}

body.modal-open {
  overflow: hidden;
}

.burger-log-out-btn {
