  overflow-y: scroll;
}

.category::-webkit-scrollbar {
  width: 8px;
  background-color: var(--bg-color);
}

.category::-webkit-scrollbar-thumb {
  background-color: var(--scroll-color);
  border-radius: 12px;
}

@media screen and (min-width: 768px) {
