.category .item {
  text-overflow: ellipsis;
  text-align: left;
  letter-spacing: -.36px;
  font-size: 18px;
  line-height: 1.33;
}

.category .item:hover, .category .item:focus {
  cursor: pointer;
  letter-spacing: -.36px;
  text-transform: uppercase;
  color: var(--sidebar-activ-text);
  text-transform: uppercase;
  white-space: nowrap;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  transition: font-weight .25s linear;
  overflow: hidden;
  transform: scaleY(1.1);
}

.category-active {
  text-transform: uppercase;
  white-space: nowrap;
  width: 100%;
